import { ShirayuriBusStop, KanazawabunkoTrain } from './time-table-entity';
import { getDiagram } from './common-utility';

/**
 * 
 */
const MAX_FILTERED_DATA = 5;

const filterTable = (date, diagram, max, table) => {
    let count = 0;
    const result = table.filter((value) => value.diagram == diagram)
        .filter((value) => {
            // false
            if (count >= max) {
                return false;
            }

            const workDate = new Date(date);
            const workTime = value.time.split(":");
            workDate.setHours(workTime[0], workTime[1]);

            if (date <= workDate) {
                count++;
                return true;
            }
            return false;
        });
    return result;
}

/**
 * 
 * @param {*} timetable 
 * @param {*} date 
 */
const filterTimeTable = (timetable, date, max) => {

    let diagram = getDiagram(date);

    let filteredTable = filterTable(date, diagram, max, timetable);

    // 
    if (filteredTable.length === max)
        return filteredTable;

    let nextDay = date.clone();
    nextDay.add(1, 'days').startOf('days');
    // 
    filteredTable.push(...filterTimeTable(timetable, nextDay, max - filteredTable.length));

    return filteredTable;
};

/**
 * 
 * @param {Array} 
 * @param {moment} 
 * @param {Number} 
 */
const getShirayuriBusStop = (date) => filterTimeTable(ShirayuriBusStop, date, 3);

/**
 * 
 * @param {Array} 
 * @param {moment} 
 * @param {Number} 
 */
const getKeikyuTrain = (date) => filterTimeTable(KanazawabunkoTrain, date, MAX_FILTERED_DATA);

export { getShirayuriBusStop, getKeikyuTrain }