import { publicHolidays } from './definition';
import moment from 'moment';

/**
 * 
 * @param {*} momentDay moment
 */
const isPublicHolidays = (momentDay) => {

    return publicHolidays.includes(momentDay.format("YYYY/M/D"));
}

/**
 * 
 * @param {*} momentDay moment
 */
const getDiagram = (momentDay) => {

    if (isPublicHolidays(momentDay)) {
        // 
        return 3;
    }

    let diagram = 1;
    // 
    switch (momentDay.isoWeekday()) {
        // 
        case 7:
            diagram = 3;
            break;
        // 
        case 6:
            diagram = 2;
            break;
        // 
        default:
            diagram = 1;
            break;
    }
    return diagram;
}

/**
 * 5Object
 * @returns {Object} {date:moment(),displayDate:"MMDD ddd",id:Date().getTime()}
 */
const makeDepartureDays = () => {
    // 
    const startOfThisMonth = moment().locale('ja').startOf('months');
    // 5
    const monthsAgo = moment().add(5, 'months').endOf('months');
    // 
    const diff = monthsAgo.diff(startOfThisMonth, "days");

    const dates = [];
    for (let i = 0; i < diff; i++) {
        const date = startOfThisMonth.clone();
        date.add(i, 'days');
        const dateProp = {
            date: date,
            id: date.toDate().getTime()
        }
        dates.push(dateProp);
    }
    return dates;
}

/**
 * 
 * @param {*} number 
 * @param {*} length 
 */
const zeroPading = (number, length) => {
    if (length < 1)
        return number;

    let str = "";

    for (let i = 1; i < length; i++) {
        str += "0";
    }
    return (str + number).slice(-length);
}

/**
 * 0
 * @param {*} number 
 */
const makeNumArray = number => {
    const array = [];
    for (let i = 0; i < number; i++) {
        array.push(zeroPading(i, Number(number).toString().length));
    }
    return array;
};
/**
 * 
 */
const makeHoursArray = () => makeNumArray(24);
/**
 * 
 */
const makeMinutesArray = () => makeNumArray(60);

export { getDiagram, makeDepartureDays, makeHoursArray, makeMinutesArray, zeroPading }